.dialogs {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-areas:
          'respondents messages'
          'respondents newMessage';
}

.respondents {
  margin: 0;
  padding: 0 10px 0 0;
  list-style-type: none;
  grid-area: respondents;
}

.messages {
  margin: 0;
  padding: 0;
  list-style-type: none;
  grid-area: messages;
}

.newMessage {
  display: flex;
  grid-area: newMessage;
}
